.page-header {
	background-size: cover;
	margin-bottom: $spacing-bottom-half;
	position: relative;

	&.bg-image {
		color: $white;
		margin-bottom: $spacing-bottom;
		padding: $spacing-bottom 0;

		.overlay {
			background-color: rgba(0, 0, 0, 0.6);
			bottom: 0;
			left: 0;
			position: absolute;
			right: 0;
			top: 0;
		}
	}
}
